<template>
  <div class="adress">
    <div class="temp-search">
      <h1></h1>
      <div class="temp-search-wrapper">
        <div class="temp-search-row">
          <label></label>
          <input class="input" type="text" v-model="wardNumber">  
        </div>
        <div class="temp-search-row">
          <label></label>
          <input class="input" type="text" v-model="bedNumber">
        </div>
        <div class="temp-search-row">
          <label></label>
          <input class="input" type="text" v-model="deviceCode">
        </div>
        <div class="temp-search-row" style="margin-top: 26px;"><span class="btn btn-search" @click="findData"><i class="fa fa-search"></i></span></div>
      </div>
    </div>
        <div class="temp-table">
      <div class="temp-table-header"> 
        <h1>
          <span  @click="export2Excel()"><i class="fa fa-download"></i> </span>
          <span class="btn-add" @click="showadd()"><i class="fa fa-plus"></i> </span>
          </h1>
      </div>
      <div class="temp-table-content">
        <table class="table">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>            
              <th></th>
              <th width='200px'></th>
            </tr>
          </thead>
          <tbody>
            <tr  v-if="onData"><th colspan="6" class="no-data"></th></tr>
            <tr v-for="(item, index) in tableData" :key="index">
              <th>{{(index+1)+((page-1) *row)}}</th>
              <th>{{item.wardNumber}}</th>
              <th>{{item.bedNumber}}</th>
              <th>{{item.deviceTypeName}}</th>
              <th>{{item.deviceCode}}</th>
              <th>{{item.deviceName}}</th>
              <th>
                <span class="fa fa-pencil-square-o edit" @click="modifyDevice(item)"></span>
                <span class="fa fa-times danger" @click="removeDevice(item)"></span>
              </th>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="6"><page :total="total" :current-page="page" @pagechange='pagechange'></page></td>
              <td colspan="1" class="item-switch-re">
                <div class="item">
                  <span class="item-label btn-refresh" @click="refreshTable()"></span>
                </div>
              </td>
            </tr>
          </tfoot>
        </table>
        <!-- loading -->
        <div v-show="isloading" class="loading-container">
          <loading title=""></loading>
        </div>
        <!-- message-box- -->
        <message-box :title="mBox.title" @hide="messageShow=false" v-if="messageShow"> 
          <form class="add-form" autocomplete="off">
            <div class="add-form-item-select" v-if="mBox.title=='' || mBox.title==''">
              <label for="deviceType"></label>
              <v-select :list="options" v-model="deviceType" :valueItem='valueItem'></v-select> 
            </div>
            <div class="add-form-item-select" v-if="mBox.title=='' || mBox.title==''">
              <label for="deviceCode"></label>
              <v-select :list="deviceCodeList" v-model="add.deviceCode" :valueItem='valueItem1'></v-select>
              <!-- <span>{{add.deviceCode}}</span> -->
            </div>
            <div class="add-form-item-input" v-if="mBox.title=='' || mBox.title==''">
              <label for="wardNumber"></label>
              <input type="text" v-model="add.wardNumber" class="input" required>
            </div>
            <div class="add-form-item-input" v-if="mBox.title=='' || mBox.title==''">
              <label for="bedNumber"></label>
              <input type="text" v-model="add.bedNumber" class="input" required>
            </div>
            <div class="add-form-del" v-if="mBox.title==''">
              
            </div>
            <div>
              <button v-if="mBox.title==''" class="btn btn-submit" @click.prevent="addDevicePos()"></button>
              <button v-if="mBox.title==''" class="btn btn-submit" @click.prevent="editDevice()"></button>
              <button v-if="mBox.title==''" class="btn btn-submit btn-del" @click.prevent="delDevice()"></button>
              <button v-if="mBox.title==''" class="btn btn-submit btn-del cancle" @click.prevent="undelDevice()"></button>
            </div>
          </form>
        </message-box>
        <!-- notice -->
        <v-notice v-if="notice.type" :type="notice.type" :info="notice.info"></v-notice>
        <!-- MessageAlert -->
        <v-dialog v-show="showDialog" :dialog-option="dialogOption" ref="dialog"></v-dialog>
      </div>
    </div>
  </div>
</template>
<script>
import {getLocation, addDevicePos, editDevicePos, removeDevicePos,devicePositions} from 'api/get-adress'
import {getDevice} from 'api/getDevice'
import Page from 'base/page/page'
import Loading from 'base/loading/loading'
import MessageBox from 'base/message-box/message-box'
import VDialog from 'base/v-dialog/v-dialog'
import TRadio from 'base/t-radio/t-radio'
import VSelect from 'base/v-select/v-select'
import VNotice from 'base/v-notice/v-notice'
  export default {
    data () {
      return {
        page:1,
        row: 10,
        total: 0,
        wardNumber:'', // 
        bedNumber:'',     // 
        deviceCode:'',  //  
        tableData: [],
        isloading:false,
        onData:'',
        options:[
          {text:'',value:'01'},
          {text:'',value:'02'},
          {text:'',value:'03'}
        ],
        deviceType: '' , // 
        add: {
          deviceCode: '',  // 
          wardNumber: '',  // 
          bedNumber:'',     // 
          devicePositionId:'',
          deviceId:''
        },
        deviceCodeList:[],
        messageShow: false,
        notice:{
          type:'',
          info:'',
        },
        dialogOption:{
          title: "?"
        },
        showDialog:true,
        valueItem:'',
        valueItem1:'',
        mBox:{
          title:'',
        },
      }
    },
    components: {
      Page,
      Loading,
      MessageBox,
      VDialog,
      TRadio,
      VSelect,
      VNotice
    },
    methods: {
      pagechange (value) {
        this.page = value
        this.getLocationData()
      },
      // 
      refreshTable(){
        // this.page = 1
        this.wardNumber = ''
        this.bedNumber = ''
        this.deviceCode = ''
        this.getLocationData()
      },
      // 
      findData () {
        this.page = 1
        this.getLocationData()
      },
      getLocationData () {
        this.isloading = true
        let mydata = {
          rows: this.row,
          page: this.page,
          wardNumber: this.wardNumber,
          bedNumber: this.bedNumber,
          deviceCode: this.deviceCode
          }
        getLocation(mydata).then((data) => {
         this.isloading = false
        //  console.log(data)
          if( data.code == '200') {
            this.total = data.total
            this.tableData = data.data
            this.onData = false
          }else if (data.code == '404'){
            this.onData = true
            this.total = 0
            this.tableData = []
          }
        }).catch((err)=>{
          // console.log(err)
          this.isloading = false
        })
      },
      // 
      getDeviceCode () {
        getDevice({deviceTypeCode: this.deviceType}).then((res)=>{
           if (res.code == '200') {
             this.deviceCodeList.length=0
             res.data.forEach((item,index)=>{
               this.deviceCodeList[index] = new Object()
               this.deviceCodeList[index].text =  item.deviceName
               this.deviceCodeList[index].value =  item.deviceId
             })
           } else {
             this.deviceCodeList.length=0
           }
        })
      },
      // 
      addDevicePos () {
        let mydata = {
          wardNumber: this.add.wardNumber,
          bedNumber: this.add.bedNumber,
          deviceId: this.add.deviceCode
        }
        // 
        if (this.add.wardNumber.length == 0){return}
        if (this.add.bedNumber.length == 0){return}
        if (this.add.deviceCode.length == 0){return}
        let that = this   
        // 
        this.notice.type = 'loading'
        this.notice.info = ''
        addDevicePos(mydata).then((res)=>{
          if (res.code == '200') {
            that.notice.type = 'success'
            that.notice.info = ''
             that.refreshTable()
            setTimeout(()=>{
              that.notice.type = ''
              that.notice.info = ''
              that.messageShow = false            
            },1000)
          }else {
            that.notice.type = 'error'
            that.notice.info = ''
            setTimeout(()=>{
              that.notice.type = ''
              that.notice.info = ''
            },1000)
          }
        }).catch((err)=>{
             that.notice.type = 'error'
            that.notice.info = ''
            setTimeout(()=>{
              that.notice.type = ''
              that.notice.info = ''
            },1000)         
        })
      },
     showadd(){
        this.messageShow=true
        this.mBox.title=``
        this.clearData()
      },
      editDevice(){
        let mydata = {
          wardNumber: this.add.wardNumber,
          bedNumber: this.add.bedNumber,
          deviceId: this.add.deviceCode? this.add.deviceCode : 0
        }
        // console.log(mydata)
        // alert(mydata.deviceId)
        // 
        if (this.add.wardNumber.length == 0){return}
        if (this.add.bedNumber.length == 0){return}

        let that = this
        // 
        this.notice.type = 'loading'
        this.notice.info = ''
        editDevicePos(this.add.devicePositionId,mydata).then((res)=>{
          if (res.code == '200') {
            // console.log('')
            that.notice.type = 'success'
            that.notice.info = ''
            that.refreshTable()
            setTimeout(()=>{
              that.notice.type = ''
              that.notice.info = ''
              that.messageShow = false
              // that.clearData()
            },1000)
          }else {
            that.notice.type = 'error'
            that.notice.info = ''
            // that.clearData()
            setTimeout(()=>{
              that.notice.type = ''
              that.notice.info = ''
            },1000)
          }
        })        
      },
      clearData () {
        this.add.deviceCode = ''
        this.add.wardNumber = ''
        this.add.bedNumber = ''
        this.add.deviceId = ''
        this.valueItem = ''
        this.valueItem1 = ''
        this.add.devicePositionId =''
        this.deviceCodeList =[]
      },
      // 
      modifyDevice (item) {
        this.mBox.title=``
         
        // 
        // 
        let deviceTypeName = ''
        let deviceTypeCode = ''
        let deviceName = ''
        let deviceId = ''
        devicePositions(item.devicePositionId).then((res)=>{
         if(res.code==200){
           if (res.data.deviceTypeId==null) {
            deviceTypeCode = ''
            deviceTypeName = ''
            deviceName = ''
            deviceId = 0
            this.add.deviceCode = 0
            
           }else {
            deviceTypeCode = res.data.deviceTypeName =='' ? '01': "03"
            deviceTypeName = res.data.deviceTypeName
            deviceName = res.data.deviceName
            deviceId = res.data.deviceId
            this.add.deviceCode = res.data.deviceId
            console.log(res.data.deviceId)
           }
            this.getDeviceCode()
            return Promise.resolve({deviceTypeName,deviceTypeCode,deviceName,deviceId})
         }
        })
        .then((data)=>{
          //  console.log(data)
            this.add.deviceTypeCode = data.deviceTypeCode
            this.add.wardNumber = item.wardNumber
            this.add.bedNumber = item.bedNumber
            this.add.deviceId = data.deviceId
            this.add.devicePositionId = item.devicePositionId
            this.valueItem = `${data.deviceTypeName}`
            this.valueItem1 = data.deviceName
            // console.log(data.deviceName)
            this.messageShow=true
        })
      },
      // 
      removeDevice(item) {
        this.messageShow=true
        this.mBox.title=``
        this.add.devicePositionId = item.devicePositionId 
      },
      // 
      delDevice() {
        let that = this
        // 
        this.notice.type = 'loading'
        this.notice.info = ''
        removeDevicePos(this.add.devicePositionId).then((res)=>{
          if (res.code == '200') {
            // console.log('')
            that.notice.type = 'success'
            that.notice.info = ''
            that.refreshTable()
            setTimeout(()=>{
              that.notice.type = ''
              that.notice.info = ''
              that.messageShow = false
              that.clearData()
            },1000)
          }else {
            that.notice.type = 'error'
            that.notice.info = ''
            setTimeout(()=>{
              that.notice.type = ''
              that.notice.info = ''
            },1000)
          }
        }) 
      },
      undelDevice(){
        this.messageShow = false
        this.clearData
      },
      export2Excel() {
      require.ensure([], () => {
      const { export_json_to_excel } = require('vendor/Export2Excel');
      const tHeader = ['', '', '', '', '',''];
      const filterVal = ['devicePositionId', 'deviceCode', 'deviceName','wardNumber','bedNumber','inpatientAreaName'];
      const list = this.tableData;
      const data = this.formatJson(filterVal, list);
      export_json_to_excel(tHeader, data, 'excel');
      })
      },
      formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
      }
    },
    created () {
      this.getLocationData()
      this.getLocationData()
    },
    watch: {
      pagechange (value) {
        this.page = value
        this.getLocationData()
      },
      deviceType (value) {
        // 
        // this.valueItem1 = 0
        // this.add.deviceCode = 0
        this.getDeviceCode()
        // 
      }
    },
    mounted () {
      this.showDialog = false;
      this.$refs.dialog.confirm().then(() => {
      this.showDialog = false;
      // next();
      }).catch(() => {
      this.showDialog = false;
      // next();
      })
    }
  }
</script>


