<template>
  <div class="temp">
    <div class="temp-search">
      <h1></h1>
      <div class="temp-search-wrapper">
        <div class="temp-search-row">
          <label></label>
          <select v-model="deviceTypeCode" class="input">
            <option v-for="(option,index) in options" v-bind:value="option.value" :key="index">
              {{ option.text }}
            </option>
          </select>
        </div>
        <div class="temp-search-row">
          <label></label>
          <input class="input" type="text" v-model="deviceName">
        </div>
        <div class="temp-search-row">
          <label></label>
          <input class="input" type="text" v-model="deviceCode">
        </div>
        <div class="temp-search-row" style="margin-top: 26px;"><span class="btn btn-search" @click="findData()"><i class="fa fa-search"></i></span></div>
      </div>
    </div>
    <div class="temp-table">
      <div class="temp-table-header"> 
        <h1>
          <span  @click="export2Excel()"><i class="fa fa-download"></i> </span>
          <span class="btn-add" @click="showadd()"><i class="fa fa-plus"></i> </span>
          </h1>
      </div>
      <div class="temp-table-content">
        <table class="table">
          <thead>
            <tr>
              <th></th>
              <th></th>
              <th></th>
              <th></th>      
              <th></th>
              <th width='200px'></th>
            </tr>
          </thead>
          <tbody>
            <tr  v-if="onData"><th colspan="6" class="no-data"></th></tr>
            <tr v-for="(item, index) in tableData" :key="index">
              <th>{{(index+1)+((page-1) *row)}}</th>
              <th>{{item.deviceCode}}</th>
              <th>{{item.deviceName}}</th>
              <th>{{item.deviceTypeName}}</th>
              <th :style="{color:item.status == 1 ? '#67C23A':'#F56C6C'}">{{item.status|formatStatus}}</th>
              <th>
                <span class="fa fa-pencil-square-o edit" @click="modifyDevice(item)"></span>
                <span class="fa fa-times danger" @click="removeDevice(item)"></span>
              </th>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="5"><page :total="total" :current-page="page" @pagechange='pagechange'></page></td>
              <td colspan="1" class="item-switch-re">
                <div class="item">
                  <span class="item-label btn-refresh" @click="refreshTable()"></span>
                </div>
                </td>
            </tr>
          </tfoot>
        </table>
        <!-- loading -->
        <div v-show="isloading" class="loading-container">
          <loading title=""></loading>
        </div>
        <!-- message-box- -->
        <message-box :title="mBox.title" @hide="messageShow=false" v-if="messageShow"> 
          <form class="add-form" autocomplete="off">
            <div class="add-form-item-select" v-if="mBox.title=='' || mBox.title==''">
              <label for="deviceType"></label>
               <v-select :list="deviceCodeList" v-model="add.deviceTypeCode" :valueItem='valueItem'></v-select> 
            </div>
            <div class="add-form-item-input"  v-if="mBox.title=='' || mBox.title==''">
              <label for="wardNumber"></label>
              <input type="text" v-model="add.deviceName" class="input" required>
            </div>
            <div class="add-form-item-input"  v-if="mBox.title=='' || mBox.title==''">
              <label for="bedNumber"></label>
              <input type="text" v-model="add.deviceCode" class="input" required>
            </div>
            <div class="add-form-del" v-if="mBox.title==''">
              
            </div>
            <div>
              <button v-if="mBox.title==''" class="btn btn-submit" @click.prevent="addDevice()"></button>
              <button v-if="mBox.title==''" class="btn btn-submit" @click.prevent="editDevice()"></button>
              <button v-if="mBox.title==''" class="btn btn-submit btn-del" @click.prevent="delDevice()"></button>
              <button v-if="mBox.title==''" class="btn btn-submit btn-del cancle" @click.prevent="undelDevice()"></button>
            </div>
          </form>
        </message-box>
        <!-- notice -->
        <v-notice v-if="notice.type" :type="notice.type" :info="notice.info"></v-notice>        
      </div>
    </div>
  </div>
</template>

<script>
import {getDevice, addDevice, editDevice, removeDevice} from 'api/getDevice'
import Page from 'base/page/page'
import Loading from 'base/loading/loading'
import MessageBox from 'base/message-box/message-box'
import TRadio from 'base/t-radio/t-radio'
import VSelect from 'base/v-select/v-select'
import VNotice from 'base/v-notice/v-notice'
  export default {
    data () {
      return {
        page:1,
        row: 10,
        total: 0,
        deviceTypeCode:'', // 
        deviceName:'',     // 
        deviceCode:'',  //  
        auto: '',
        tableData: [],
        isloading:false,
        onData: false,
        options:[
          {text:'',value:'01'},
          {text:'',value:'02'},
          {text:'',value:'03'},
        ],
        add: {
          deviceTypeCode: '',  // 
          deviceName: '',  // 
          deviceCode:'' ,    // ,
          deviceId:''
        },
        deviceCodeList:[
          {text:'',value:'01'},
          {text:'',value:'02'},
          {text:'',value:'03'}
          ],
        messageShow: false,
        // config
        valueItem:'',
        mBox:{
          title:'',
        },
        notice:{
          type:'',
          info:'',
        }
      }
    },
    filters:{
      formatStatus(status) {
        if (status == 0) {
          return ''
        }else {
          return ''
        }
      }
    },
    components: {
      Page,
      Loading,
      MessageBox,
      TRadio,
      VSelect,
      VNotice
    },
    methods: {
      pagechange (value) {
        this.page = value
        this.getDeviceData()
      },
      // 
      refreshTable(){
        // this.page = 1
        this.deviceTypeCode = ''
        this.deviceName = ''
        this.deviceCode = ''
        this.getDeviceData()
      },
      // 
      findData () {
        this.page = 1
        this.getDeviceData()
      },
      getDeviceData () {
        this.isloading = true
        let mydata = {
          rows: this.row,
          page: this.page,
          deviceTypeCode: this.deviceTypeCode,
          deviceName: this.deviceName,
          deviceCode: this.deviceCode
          }
        getDevice(mydata).then((data) => {
         this.isloading = false
        //  console.log(data)
          if( data.code == '200') {
            this.total = data.total
            this.tableData = data.data
            this.onData = false
          }else if (data.code == '404'){
            this.onData = true
            this.total = 0
            this.tableData = []
          }
        }).catch((err)=>{
          // console.log(err)
          this.isloading = false
        })
      },
      showadd(){
        this.messageShow=true
        this.mBox.title=``
        this.clearData()
      },
      // 
      addDevice () {
        let mydata = {
          deviceTypeCode: this.add.deviceTypeCode,
          deviceName: this.add.deviceName,
          deviceCode: this.add.deviceCode
        }
        // console.log(mydata)
        // 
        if (this.add.deviceName.length == 0){return}
        if (this.add.deviceCode.length == 0){return}

        let that = this
        
        // 
        this.notice.type = 'loading'
        this.notice.info = ''
        addDevice(mydata).then((res)=>{
          if (res.code == '200') {
            // console.log('')
            that.notice.type = 'success'
            that.notice.info = ''
            that.refreshTable()
            setTimeout(()=>{
              that.notice.type = ''
              that.notice.info = ''
              that.messageShow = false             
            },1000)
          }else {
            that.notice.type = 'error'
            that.notice.info = ''
            setTimeout(()=>{
              that.notice.type = ''
              that.notice.info = ''
            },1000)
          }
        })
      },
      editDevice(){
        let mydata = {
          deviceTypeCode: this.add.deviceTypeCode,
          deviceName: this.add.deviceName,
          deviceCode: this.add.deviceCode
        }
        // console.log(mydata)
        // 
        if (this.add.deviceName.length == 0){return}
        if (this.add.deviceCode.length == 0){return}

        let that = this
        // 
        this.notice.type = 'loading'
        this.notice.info = ''
        editDevice(this.add.deviceId,mydata).then((res)=>{
          if (res.code == '200') {
            // console.log('')
            that.notice.type = 'success'
            that.notice.info = ''
            that.refreshTable()
            setTimeout(()=>{
              that.notice.type = ''
              that.notice.info = ''
              that.messageShow = false
              that.clearData()
            },1000)
          }else {
            that.notice.type = 'error'
            that.notice.info = ''
            setTimeout(()=>{
              that.notice.type = ''
              that.notice.info = ''
              that.clearData()
            },1000)
          }
        })        
      },
      clearData () {
        this.add.deviceTypeCode = ''
        this.add.deviceName = ''
        this.add.deviceCode = ''
        this.add.deviceId = ''
        this.valueItem =''
      },
      // 
      modifyDevice (item) {
        // console.log(item)
        this.messageShow=true
        this.mBox.title=``
        // 
        // console.log(item.deviceTypeName)
        if(item.deviceTypeName ==''){
          this.add.deviceTypeCode = '01'
        }else if (item.deviceTypeName =='') {
          this.add.deviceTypeCode = '02'
        }else if (item.deviceTypeName ==''){
          this.add.deviceTypeCode = '03'
        }
        
        this.add.deviceName = item.deviceName
        this.add.deviceCode = item.deviceCode
        this.add.deviceId = item.deviceId
        this.valueItem = `${item.deviceTypeName} `
      },
      // 
      removeDevice(item) {
        this.messageShow=true
        this.mBox.title=``
        this.add.deviceId = item.deviceId 
      },
      // 
      delDevice() {
        let that = this
        // 
        this.notice.type = 'loading'
        this.notice.info = ''
        removeDevice(this.add.deviceId).then((res)=>{
          if (res.code == '200') {
            // console.log('')
            that.notice.type = 'success'
            that.notice.info = ''
            that.refreshTable()
            setTimeout(()=>{
              that.notice.type = ''
              that.notice.info = ''
              that.messageShow = false
              that.clearData()
            },1000)
          }else {
            that.notice.type = 'error'
            that.notice.info = ''
            setTimeout(()=>{
              that.notice.type = ''
              that.notice.info = ''
            },1000)
          }
        }) 
      },
      undelDevice(){
        this.messageShow = false
        this.clearData
      },
      export2Excel() {
      require.ensure([], () => {
      const { export_json_to_excel } = require('vendor/Export2Excel');
      const tHeader = ['', '', '', '', '',''];
      const filterVal = ['deviceId', 'deviceCode', 'deviceName','deviceTypeName','status','remark'];
      const list = this.tableData;
      const data = this.formatJson(filterVal, list);
      export_json_to_excel(tHeader, data, 'excel');
      })
      },
      formatJson(filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
      }
    },
    created () {
      this.getDeviceData()
      // console.log(this.tableData)
    },
    watch: {
      pagechange (value) {
        this.page = value
        this.getDeviceData()
      }
    }
  }
</script>


